<template>
  <form class="login-form form-inline" @submit.prevent="handleLogin()">
    <input type="text" class="form-control mr-3 login-form__text" v-model="authData.phone" required
           id="phone" aria-describedby="phoneHelp" placeholder="Mobile Number">
    <div class="input-group mr-2">
      <input type="password" class="form-control login-form__text login-form__text_password" v-model="authData.password"
             required id="password" placeholder="Password">
      <div class="input-group-append forgot-link">
        <a @click.prevent="$router.push({name: 'reset_password'})">Forgot?</a>
      </div>
    </div>
    <loading-indicator v-if="isLoading"></loading-indicator>
    <button v-if="!isLoading" type="submit" class="btn btn-primary btn__login mr-2"
            :disabled="!authData.phone || !authData.password">Login
    </button>
    <button @click.prevent="$router.push({name: 'register'})" class="btn btn-primary btn__register">
      Register
    </button>
  </form>

</template>

<script>
  import axios from 'axios'
  import {LOGIN_URL} from '../../api_routes'
  import {AUTH_CONFIG} from '../../auth_config'
  import LoadingIndicator from "../Utilities/LoadingIndicator";

  export default {
    name: 'LoginForm',
      components: {LoadingIndicator},
      data () {
      return {
        opt: false,
        submitted: false,
        isLoading: false,
        authData: {
          phone: '',
          password: '',
          client_id: AUTH_CONFIG.CLIENT_ID,
          client_secret: AUTH_CONFIG.CLIENT_SECRET,
          grant_type: AUTH_CONFIG.GRANT_TYPE
        },
        loginResponseData: null
      }
    },
    methods: {
      handleLogin () {
        this.isLoading = true
        axios.post(LOGIN_URL, this.authData).then((response) => {
          let loginResponseData = response.data
          if (loginResponseData.code === 200) {
            let token = loginResponseData.token
            window.localStorage.setItem('token', JSON.stringify(token))
            this.$store.dispatch('init_auth_user', {request: 'refresh'})
            this.$router.push({name: 'home'})
            this.$noty.success(loginResponseData.message)
            this.isLoading = false
          } else {
            this.$noty.error('Unable to log you in. Please check to make sure the phone number and password combination is correct')
              this.isLoading = false
          }
        }, (error) => {
          let responseError = error.response.data
          let message = responseError.errors.phone ? responseError.errors.phone[0] : ''
          this.isLoading = false
          this.$noty.error(responseError.message + '<br />' + message)
        })
      }
    }
  }
</script>

